.work {
  display: flex;
  justify-content: space-between;
  padding-bottom: 144px;

  .apply {
    max-width: 556px;

    label {
      position: relative;
      display: block;
      margin-bottom: 22px;

      img {
        position: absolute;
        right: 0;
        bottom: 13px;
        width: 24px;
        height: 24px;
        cursor: pointer;
      }
    }

    span {
      display: block;
      margin-bottom: 9px;
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.01em;
    }

    input {
      display: block;
      padding: 0 0 9px;
      width: 100%;
      outline: none;
      border: 0;
      border-bottom: 1px solid $black;
      font-weight: 600;
      font-size: 24px;
      letter-spacing: -0.02em;
      background-color: transparent;

      &::placeholder {
        opacity: 0.25;
        color: $black;
      }
    }

    button {
      margin-right: 16px;
      padding: 0 52px;
      min-height: 52px;
      border: 2px solid $black;
      border-radius: 25px;
      font-weight: 700;
      font-size: 14px;
      line-height: 20px;
      background-color: transparent;
      cursor: pointer;
    }
  }
}

.vacancy {
  padding: 63px 80px 95px;
  width: 100%;
  height: fit-content;
  max-width: 960px;
  border-radius: 24px;
  background-color: $green;

  h3 {
    margin-bottom: 12px;
    max-width: 676px;
    font-weight: 700;
    font-size: 64px;
    line-height: 72px;
    letter-spacing: -0.02em;
  }

  .location {
    margin-bottom: 75px;
    color: $black;

    span {
      &:not(:last-child) {
        &::after {
          background-color: $black;
        }
      }
    }
  }
}

.work-info {
  padding: 34px 38px;
  width: 100%;

  button {
    position: relative;
    display: block;
    margin-bottom: 66px;
    margin-left: auto;
    padding-left: 36px;
    border: 0;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.01em;
    background-color: transparent;
    cursor: pointer;

    &::before {
      content: url("../images/arrow-left.svg");
      position: absolute;
      left: 0;
      top: 50%;
      width: 24px;
      height: 24px;
      transform: translateY(-50%);
    }
  }

  h3 {
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -0.02em;
  }

  h4 {
    margin-bottom: 34px;
    font-weight: 700;
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.02em;
  }

  ol {
    margin-bottom: 40px;
    list-style: none;

    li {
      position: relative;
      padding-left: 18px;
      font-weight: 600;

      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: $green;
        transform: translateY(-50%);
      }

      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }
  }

  .job-type {
    margin-bottom: 33px;
  }

  &__wrapper {
    margin: 0 auto;
    width: 100%;
    max-width: 600px;
    border-bottom: 1px solid $black;
  }

  &__place {
    display: flex;
    margin-bottom: 50px;

    li {
      width: 100%;
      max-width: 200px;

      &:not(:last-child) {
        margin-right: 24px;
      }

      span {
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -0.01em;
        color: $grey-text;
      }

      p {
        font-weight: 700;
        font-size: 18px;
        line-height: 28px;
        letter-spacing: -0.01em;
      }
    }
  }
}